import React, { useEffect } from 'react'
import styled from 'styled-components'

import { NavigationButton } from './shared/NavigationElements'

const Dropdown = styled.div.attrs(props => ({ 
  className: 'dropdown'
}))`
  display: inline-block;
  position: relative;
`

const DropdownMenu = styled.ul.attrs(props => ({ 
  className: 'dropdown-menu'
}))`
  display: none;
  position: absolute;
  right: 8px;
  background-color: #ffffff;
  box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
  border-radius: 4px;
  padding: 8px 0;

  overflow: hidden;
  min-width: max-content;
  z-index: 1;

  list-style-type: none;
`

const Username = styled.li`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 16px;
  letter-spacing: .009375em;
  font-size: 15px;
  line-height: 1.5rem;

  height: 48px;
  max-width: 170px;

  overflow: hidden;

  &:hover {
    cursor: default;
  }
`

const Option = styled.li`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 16px;
  letter-spacing: .009375em;
  font-size: 15px;
  line-height: 1.5rem;

  height: 48px;
  background-color: #ffffff;

  overflow: hidden;

  &:hover {
    background-color: #F5F5F5;
    cursor: pointer;
  }
`

const SettingsButton = ({ username, logout }) => {
  const toggleSettings = (e) => {
    e.preventDefault()

    const dropdowns = document.getElementsByClassName("dropdown-menu")
    for (let dropdown of dropdowns) {
      getComputedStyle(dropdown).display === 'block' ?
        dropdown.style.display = 'none' :
        dropdown.style.display = 'block'
    }
  }

  //Close dropdown when user clicks outside of dropdown area
  useEffect(() => {
    const script = document.createElement('script')
    script.type = 'text/javascript'
    script.async = true
    script.innerHTML = `
    window.onclick = (event) => {
      if (!event.target.matches('.dropdown-btn')) {
        const dropdowns = document.getElementsByClassName("dropdown-menu")
        for (let dropdown of dropdowns)
          if (getComputedStyle(dropdown).display === 'block')
            dropdown.style.display = 'none'
      }
    }`

    document.body.appendChild(script)

    return () => {
      document.body.removeChild(script);
    }
  })

  return (
    <Dropdown>
      <NavigationButton onClick={toggleSettings} dropdown={true}>settings</NavigationButton>
      <DropdownMenu>
        <Username>{username}</Username>
        <Option onClick={logout}>Logout</Option>
      </DropdownMenu>
    </Dropdown>
  )
}

export default SettingsButton